.cartitems{
    margin:100px 170px;
}
.cartitems hr{
    height: 1px;
    background: rgb(137, 137, 137);
    border: 0;
}
.cartitems-format-main{
    display: grid;
    grid-template-columns:0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: black;
    font-size: 18px;
    font-weight: 600;
}
.carticon-product-icon{
    height: 62px;
}
.cart-remove-icon{
    width: 15px;
    margin: 0px 40px;
    cursor: pointer;
}
.cartitems-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: white;
}
.cartitems-down{
    display: flex;
    margin: 100px 0px;
}
.cartitems-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
}
.cartitems-total-item{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.cartitems-total button{
    width: 262px;
    height:58px;
    outline: none;
    border: none;
    background: rgb(1, 63, 105);
    color: #ebebeb;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.cartitems-promocode{
    flex: 1;
    font-weight: 16px;
    font-weight: 500;
}
.cartitems-promocode p{
    color: rgb(95, 94, 94);
}
.cartItem-promobox{
    width: 500px;
    margin-top: 15px;
    padding-left: 20px;
    height: 58px;
    background: #ebebeb;
}
.cartItem-promobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 300px;
    height: 50px;
}
.cartItem-promobox button{
    width: 195px;
    height: 58px;
    font-size: 16px;
    background: black;
    color: #ebebeb;
    cursor: pointer;
}
